import React from "react";
import colorList from "./colorList";
import "./Typehead.css";


function Typehead () {
const [searchField, setSearchTerm] = React.useState("");
const [searchResults, setSearchResults] = React.useState([]);
const handleChange = event => {
    setSearchTerm(event.target.value);
}


React.useEffect(() =>{
    const results = colorList.filter(color =>
        color.toLowerCase().includes(searchField.toLowerCase())
        );
        setSearchResults(results);
}, [searchField] );

    

if(searchField === ""){
     return(
        <div>
            <input type="text" placeholder="Search color" value={searchField} onChange={handleChange} />
        
        </div>
        )
        

    }
else{
    return(
        <div>
            <input type="text" placeholder="Search color" value={searchField} onChange={handleChange} className="b--light-blue bw2" />
        
            <ul> 
                {searchResults.map(item => (
                    <li className="grow"> {item} </li>
                ))}
            
            </ul>
        </div>
        
        )
    }

}

export default Typehead;